<template>
  <section class="main" :style="{ backgroundImage : `url(${mainBg})`}">
    <dl class="info">
      <dt>{{ $store.state.item[num].movieName }}</dt>
      <dd>{{ $store.state.item[num].intro }}</dd>
      <dd>{{ $store.state.item[num].grade }}</dd>
    </dl>
    <router-link to="/list">리스트</router-link>
  </section>
</template>

<script>

export default {
  name: 'main-vue',
  data() {
    return {
      num : 0,
      mainBg : '',
      winWidth: 0,
    }
  },
  mounted(){
  // 0~10 사이 랜덤숫자 
  this.num = Math.floor(Math.random() * 11);
  

  
  },
  created() {
    window.addEventListener('resize', this.handleResize);
    this.handleResize();
  },
  unmounted() {
    window.removeEventListener('resize', this.handleResize);
  },
  methods: {
      handleResize() {
          this.winWidth = window.innerWidth;
          if(this.winWidth < 768) { //모바일사이즈인 경우
            this.mainBg = this.$store.state.item[this.num].mPoster;
          }else {
            this.mainBg = this.$store.state.item[this.num].wPoster;
          }
      }
  }
}
</script>
